/**
 * Fetch API  
 * @class
 */
export class FetchResponse<T> {
	statusCode: number;
	response: Promise<T>;
	/**
	 * @param { number } statusCode HTTP 
	 * @param { Promise<T> } response fetch API 
	 */
	constructor(statusCode: number, response: Promise<T>) {
		this.statusCode = statusCode;
		this.response = response;
	}
}