import Fetch from 'controllers/fetch';

/**
@param { none }
@version 1
 */

export const getQuestion = () => {
  
  return Fetch("/api/v1/admin/question",'GET')
    .then(res => {
      return res.results;
    })
    .catch(err => {
      return Promise.reject(err);
    })
}

/**
@param { title, address, telephone, category, open, tag, signboard, sample } , , , , , , 
@version 1
 */
export const addCompany = (company:FormData) => {
  
  return Fetch("/api/v1/admin/company", "POST", company)
      .then(res => {
        return res.success;
      })
      .catch(err => {
        return Promise.reject(err);
      })
}

/**
@param { title } 
@version 1
 */
export const deleteCompany = (title: string) => {
  const sendData = {
    title
  }
  return Fetch("/api/v1/admin/company","DELETE", sendData)
    .then(res => {
      return res.success;
    })
    .catch(err => {
      return Promise.reject(err);
    })
}

/**
 @param { id }  
 @version 1
 */
export const dropUser = (user_id:string) => {
  const sendData = {
    user_id
  }
  return Fetch("/api/v1/admin/user","DELETE", sendData)
    .then(res => {
      if(res.success) {
        return res.results;
      }
    })
    .catch(err => {
      return Promise.reject(err);
    })
}

/**    
@param { none }    
@version 1
*/
export const allUserInfo = () => {
  return Fetch('/api/v1/admin/user','GET')
      .then(async(res) => {
        if(res.success) {
          return res.results;
        }
      })
      .catch(err => {
        return Promise.reject(err);
      })
}
