import styled from 'styled-components';
import color from 'lib/styles/pallettes';

const File = ({filename}:IFile) => {

  const reg = filename.split('.');

  return (
    <>
      {
        reg[1] !== "pdf" ?
        <Image src={`http://3.18.72.134:5000/api/v1/user/file/${filename}`} alt="" />
        : <Image src="images/Noimage_small.png" alt="" />
      }
    </>
  )
}

const Container = styled.div`
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: left;
  align-items: center;
`;

const Image = styled.img`
  width: 80px;
  height: 80px;
  margin-right: 30px;
`;

interface IFile {
  filename : string;
}

export default File;