import {  useState } from 'react';
import {Link} from 'react-router-dom';

import Button from "components/auth/signin/Button";
import Container from "components/auth/signin/Container";

import Navigation from "components/auth/signin/Navigation";
import Title from "components/auth/signin/Title";


import {IUser} from 'lib/interfaces/auth';
import { signInUser } from 'controllers/auth';
import Input from 'components/auth/signin/Input';
import Error from 'components/auth/signup/Error';


export const SignIn = () => {

  const [id, setId] = useState<IUser>({
    error : false,
    value : '',
    active: false
  });

  const [pwd, setPwd] = useState<IUser>({
    error : false,
    value : '',
    active: false
  });

  const signIn = async() => {
    signInUser(id.value, pwd.value)
    .then((res) => {
      if(res) {
       window.location.href="/";
      } else {
        setId({...id, error:!res});
      }
    })
  }

  const handleKeyPress = (k:any) => {
    if(k.key==="Enter") signIn();
  }
  
  return (
    <>
      <Container onKeyPress={(k) => handleKeyPress(k)}>
      <Title>
         <br />
          .
      </Title>
      {id.error && <Error>  .</Error>}
      <Input type="text" placeholder=" " 
            onChange={(e:any) => setId({...id, value:e.target.value})}
      />
      <Input type="password" placeholder=" " 
            onChange={(e:any) => setPwd({...pwd, value:e.target.value})} 
      />
      <Button onClick={() => signIn()}></Button>
      <Navigation>
        <li></li>
        <li> </li>
        <li><Link to="/signup"></Link></li>
      </Navigation>
      </Container>
    </>
  )
}

export default SignIn;